 .tui-select-box {
    position: relative;
}

.tui-select-box-input,
.tui-select-box-dropdown,
.tui-select-box-item-group-label,
.tui-select-box-item {
    box-sizing: border-box;
}

.tui-select-box-input {
    border: 1px solid #ddd;
}

.tui-select-box-input.tui-select-box-open,
.tui-select-box-dropdown {
    border: 1px solid #aaa;
}

.tui-select-box-input,
.tui-select-box-dropdown {
    background: #fff;
}

.tui-select-box-input,
.tui-select-box-item-group-label,
.tui-select-box-item {
    padding: 0 8px;
    height: 29px;
    font-size: 13px;
    color: #333;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
}

.tui-select-box-placeholder,
.tui-select-box-item-group-label,
.tui-select-box-item {
    line-height: 29px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tui-select-box-placeholder,
.tui-select-box-icon {
    height: 100%
}

.tui-select-box-placeholder {
    display: inline-block;
    margin: 0;
    width: 80%;
    width: calc(100% - 12px);
    vertical-align: sub;
}

.tui-select-box-icon {
    display: block;
    float: right;
    background: url(data:image/png;base64,) left center no-repeat;
    width: 7px;
    overflow: hidden;
    text-indent: 100%;
}

.tui-select-box-open > .tui-select-box-icon {
    background-position: right center;
}

.tui-select-box-dropdown,
.tui-select-box-item-group {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.tui-select-box-dropdown {
    position: absolute;
    border-top: none;
    /* max-height: 145px; */
    overflow: auto;
    z-index: 1;
    width: 100%;
    overflow-x: hidden;
}

.tui-select-box-item-group-label {
    display: block;
    font-weight: bold;
    cursor: default;
}

.tui-select-box-item-group > .tui-select-box-item {
    padding-left: 20px;
}

.tui-select-box-selected {
    background: #f4f4f4;
}

.tui-select-box-highlight {
    background: #e5f6ff;
    outline: none;
}

.tui-select-box-disabled {
    background: #f9f9f9;
    color: #c8c8c8;
    cursor: default;
}

.tui-select-box-hidden {
    display: none; /* for test */
}

#tui-grid {
