.tui-wrap01 label.form.checkbox {
  display: inline-block;
  position: relative;
  min-height: 16px;
  cursor: pointer;
  line-height: 20px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.tui-wrap01 label.form.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.tui-wrap01 label.form.checkbox .checkmark {
  position: absolute;
  top: 4px;
  left: 4px;
  height: 16px;
  width: 16px;
  background-color: var(--check-bg);
  border: 1px solid var(--color-base-050);
  border-radius: 4px;
  box-sizing: border-box;
}
.tui-wrap01 label.form.checkbox:hover input ~ .checkmark {
  background-color: var(--check-bg);
}
.tui-wrap01 label.form.checkbox:hover input ~ .checkmark::before {
  content: "";
  display: block;
  position: absolute;
  top: -3px;
  left: -3px;
  width: 20px;
  height: 20px;
  background: transparent;
  border: 2px solid #f8f8f9;
  border-radius: 6px;
  box-sizing: border-box;
}
.tui-wrap01 label.form.checkbox input:checked ~ .checkmark {
  border: 1px solid var(--color-primary-010);
  background-color: var(--color-primary-010);
}
.tui-wrap01 label.form.checkbox .checkmark::after {
  content: "";
  position: absolute;
  display: block;
  left: 5px;
  top: 2px;
  width: 3px;
  height: 6px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.tui-wrap01 label.form.checkbox input:checked ~ .checkmark::after {
  display: block;
  border: solid var(--check-bg);
  border-width: 0 1px 1px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.tui-wrap01 label.form.checkbox input:disabled ~ .checkmark {
  background: var(--check-disable);
  border: 1px solid var(--check-line);
}
.tui-wrap01 label.form.checkbox input:disabled ~ .checkmark::after {
  display: block;
  border: solid var(--check-line);
  border-width: 0 1px 1px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.tui-wrap01 label.form.checkbox input:disabled ~ .tx_lab {
  color: var(--accent-02);
}
.tui-wrap01 label.form.checkbox input:checked:disabled ~ .checkmark {
  border: 1px solid var(--radio-mark);
  background-color: var(--radio-mark);
  opacity: 0.5;
}
.tui-wrap01 label.form.checkbox input:focus-visible ~ .checkmark {
  border: 2px solid var(--color-primary-010);
  box-sizing: border-box;
}
.tui-wrap01 label.form.checkbox input:focus-visible ~ .checkmark::after {
  left: 4px;
  top: 1px;
}

